@import url(https://fonts.googleapis.com/css2?family=Yusei+Magic&display=swap);
body {
  margin: 0;
  font-family:'Yusei Magic', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a{
  text-decoration: none;
  color: inherit;
};

/* 
    "*" sign below line represnts "for all element"
    we have given "border-box" to box-sizing beacuse of padding and margin 
    in email and pass we are facing allignment issue 
*/
*{
  box-sizing: border-box;
  font-family: 'Yusei Magic', sans-serif;
  }
.survey-container{width:100%;min-height:74vh;margin-top:-10vh;display:flex;flex-direction:column;align-items:center;justify-content:space-around;position:relative}.survey-data-error{font-size:2rem;color:#f40;padding:0px 30px;font-weight:bold}.survey-no-data{font-size:2rem;color:#04f;padding:0px 30px;font-weight:bold}.survey-response-container{width:50%;padding:20px;margin:0px auto 50px;border:1px solid;box-shadow:5px 5px 17px #000;border-radius:5px;font-size:2rem;color:#00969b}.survey-response-container .eachSurvey{display:flex;justify-content:space-between}.survey-response-container .eachSurveyHeader{display:flex;justify-content:center;margin-bottom:10px;color:crimson;font-weight:bold}.survey-response-container .eachSurveyResponse{width:40%}.add-new-survey-iocn{z-index:100;position:relative;position:-webkit-sticky;position:sticky;top:95vh;left:90vw}.add-new-survey-iocn:hover{-webkit-filter:brightness(1.1);filter:brightness(1.1);cursor:pointer}.add-new-survey-iocn:active{-webkit-transform:scale(0.95);transform:scale(0.95)}
.background-image{background-position:50% 10%;min-height:89.5vh;background-size:cover;-webkit-filter:brightness(0.8);filter:brightness(0.8)}@media screen and (max-width: 1000px){.background-image{-webkit-filter:brightness(1.3);filter:brightness(1.3)}}.home-header{display:flex;justify-content:center;font-size:4rem;font-weight:800;color:brown;font-family:"Yusei Magic",sans-serif}@media screen and (max-width: 1000px){.home-header{padding-top:20px;font-size:2rem;color:#000}}@media screen and (max-width: 500px){.home-header{font-size:1.8rem}}.home-subheader{display:flex;font-size:2rem;color:#000;font-family:"Yusei Magic",sans-serif;padding:30px;font-weight:800}@media screen and (max-width: 1000px){.home-subheader{display:none}}.home-content{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;display:flex;flex-direction:column;margin:0px 40px;font-size:2rem;color:#6495ed;font-weight:800;font-family:"Yusei Magic",sans-serif}.home-content li:hover{color:#f15e9c;cursor:pointer}@media screen and (max-width: 1000px){.home-content{margin-top:5vh;color:brown;font-size:1.5rem}}
.error{display:flex;flex-direction:column;justify-content:flex-start;align-items:center;font-size:2rem;font-family:"Yusei Magic",sans-serif;color:#ff0059;font-weight:bold}
.profile-container{display:flex;flex-direction:row;justify-content:space-around;background-color:rgba(226,226,226,.8);width:100%;height:89vh;align-items:center}@media screen and (max-width: 600px){.profile-container{flex-direction:column;height:79vh}}.profile-container .profile-picture{width:300px;height:300px;box-shadow:5px 5px 15px #9fbffc}@media screen and (max-width: 1000px){.profile-container .profile-picture{height:30vw;width:30vw}}.profile-container .user-content{margin-left:100px;display:flex;flex-direction:column;align-items:center;width:600px;height:300px;box-shadow:5px 5px 15px #6495ed}@media screen and (max-width: 1000px){.profile-container .user-content{margin-left:5vw;height:30vw;width:60vw}}.profile-container .user-content .user-field{display:flex;justify-content:space-around;font-size:2rem;width:inherit;padding-bottom:20px}@media screen and (max-width: 1000px){.profile-container .user-content .user-field{font-size:3vw}}.profile-container .user-content .user-field .user{width:50%;word-break:break-all}
.new-form-error-container{display:flex;flex-direction:column;justify-content:flex-start;align-items:center;font-family:"Yusei Magic",sans-serif;color:#ff0059;font-weight:bold;background-color:rgba(226,226,226,.8);height:80vh;padding-top:60px}.add-credit-form{font-size:3rem;color:blue;padding:40px}.add-credit-form:hover{color:#fc8585;cursor:pointer}.add-credit-form:active{-webkit-transform:scale(0.95);transform:scale(0.95)}.add-new-survey-form-container{display:flex;flex-direction:column;justify-content:center;align-items:center}.add-new-survey-form-container .form-container{width:70vw;border:1px solid #000;box-shadow:10px 10px 10px #000;border-radius:10px;background-color:rgba(226,226,226,.8)}.add-new-survey-form-container .form-container .form-header{align-self:center;font-family:"Yusei Magic",sans-serif;font-weight:bold;font-size:3rem;display:flex;justify-content:center;padding:20px 20px 0px}@media screen and (max-width: 900px){.add-new-survey-form-container .form-container .form-header{font-size:1.5rem}}.add-new-survey-form-container .form-container .submit-button-container{display:flex;justify-content:center;align-items:flex-end;padding:10px 50px;margin:10px 0px}.add-new-survey-form-container .form-container .submit-button-container .button{background-color:#6495ed;color:#000;font-family:"Yusei Magic",sans-serif;font-size:1.5rem;padding:15px 20px;border-radius:5px;box-shadow:2px 2px 2px}.add-new-survey-form-container .form-container .submit-button-container .button:hover{-webkit-filter:brightness(1.1);filter:brightness(1.1);cursor:pointer}.add-new-survey-form-container .form-container .submit-button-container .button:focus{outline:none}.add-new-survey-form-container .form-container .submit-button-container .button:active{-webkit-transform:scale(0.95);transform:scale(0.95)}.test-card-details-container{color:blue;padding:10px;border:5px solid #000;border-radius:5px;display:flex;flex-direction:column;align-items:center;width:60%}.each-test-key{display:flex;width:100%;justify-content:space-between;margin-left:60px;flex-direction:row}
.group{position:relative;margin:30px}.group .form-input{display:block;width:95%;background:none;background-color:rgba(226,226,226,.8);color:#000;font-size:18px;padding:10px 10px 10px 5px;margin:25px 0;border:none;border-radius:0;border-bottom:1px solid #000;font-family:"Yusei Magic",sans-serif}.group .form-input:focus{outline:none}.group .form-input:focus~.form-input-label{top:-14px;font-size:12px;color:#000}.group input[type=password]{letter-spacing:.3em}.group .form-input-label{position:absolute;left:5px;top:10px;color:#000;font-size:16px;font-weight:normal;pointer-events:none;transition:300ms ease all;font-family:"Yusei Magic",sans-serif}.group .form-input-label.shrink{top:-14px;font-size:12px;color:#000}
.form-button-container{display:flex;justify-content:space-around;align-items:flex-end;padding:10px 50px;margin:20px 40px}@media screen and (max-width: 1100px){.form-button-container{margin:0px 0px}}.form-button-container .stepper-button{background-color:#ffa4a4;color:#000;font-family:"Yusei Magic",sans-serif;font-size:1.5rem;padding:15px 20px;border-radius:5px;box-shadow:2px 2px 2px}.form-button-container .stepper-button:hover{-webkit-filter:brightness(1.1);filter:brightness(1.1);cursor:pointer}.form-button-container .stepper-button:focus{outline:none}.form-button-container .stepper-button:active{-webkit-transform:scale(0.95);transform:scale(0.95)}.final-button{color:coral}.final-button:hover{-webkit-filter:brightness(1.5);filter:brightness(1.5);cursor:pointer}.final-button:focus{outline:none}.final-button:active{-webkit-transform:scale(0.95);transform:scale(0.95)}
.formDataContainer{display:flex;justify-content:center;align-items:center}.formDataContainer .userDataContainer{background-color:#f5e5f5;padding:20px;width:70vw;min-height:60vh;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;display:flex;flex-direction:column;align-items:center}.formDataContainer .userDataContainer .eachData{display:flex;margin:20px 50px;width:80%;flex-wrap:wrap;justify-content:space-between;font-family:"Yusei Magic",sans-serif;font-weight:bold;font-size:2rem}
